// ************* IMPORT MODULES ********* //
import { $map } from "../components/globalSelect";

// ************* INSTANTIATE OBJECTS ****** *//

//************* CLASSES DECLARATION ********* */
class HereMaps {
  load(mapContainer) {
    // intialize the HERE Maps Apis
    var platform = new H.service.Platform({
      app_id: "qE0mA090Xy882e25rzOe",
      app_code: "cUqRE9iFoQaTWkTBquoAIQ"
    });

    // ****** instantiate the map object ****** //
    // 0. get the type of the map to display
    var defaultMapLayers = platform.createDefaultLayers();

    // 1. display the map from the map object
    new H.Map(mapContainer, defaultMapLayers.normal.map, {
      zoom: 10,
      center: { lat: 52.5, lng: 13.4 }
    });
  }
}

//************** EXPORT ********************* */
export default HereMaps;
