// ************** IMPORT MODULES ************//
import HereMaps from "../components/map";
import { $map } from "../components/globalSelect";

// ************* INSTANTIATE OBJECTS ****** *//
const $S = require("scriptjs");
const handleMap = new HereMaps();
// initialize all HERE Maps Modules we will need to display the map

// 1. core (contains the core functionality)
const core = "https://js.api.here.com/v3/3.0/mapsjs-core.js";

// load HERE Maps core (contains the core functionality)
$S(core, function() {
  console.log("read");
  handleMap.load($map);
});
